function Home(){
  let headImg = React.createRef();
  let header = React.createRef();
  let blogger = React.createRef();
  let thenavigation = React.createRef();


  const handleScroll=()=>{
    if(window.pageYOffset>30){
      headImg.current.classList.add('hideHeader');
      thenavigation.current.classList.add('navbarshow');
      header.current.classList.add('App-with-blueBG');
      blogger.current.classList.add('blogUp');
    }
    else{
      headImg.current.classList.remove('hideHeader');
      thenavigation.current.classList.remove('navbarshow');
      header.current.classList.remove('App-with-blueBG');
      blogger.current.classList.remove('blogUp');
    }
  }
  window.addEventListener('scroll', handleScroll);

  const removeListener= ()=>window.removeEventListener('scroll',handleScroll);

  return (
    <div className="App" onScroll={handleScroll}>
      <div ref={thenavigation} className="navbar">
        <b className="sketchy">SKETCHY</b><b className="rhymes">RHYM.ES</b>
      </div>
      <div>
        <header ref={header} className="App-header">
          <img ref={headImg} src={logo} className="App-logo" alt="logo" />
          <span className="scrollDown">SCROLL DOWN</span>
        </header>
      </div>
      <div ref={blogger} className="blog">  
          {Object.keys(posts).map(item => <FeedPost rem={removeListener} post={item}/>)}
      </div>
      <footer>
        Crafted with <b>love</b> by Shikhar
      </footer>
    </div>
  );
}
function App() {
  const [post, setPost] = useState(-1);
  return (
    <div className="App">
      <Router>
        <div>
          <ScrollMemory />
          <Route path="/" exact component={Home} />
          <Route path="/post/:id" component={BlogPost} />
        </div>
      </Router>
    </div>
  );
}

function FeedPost(props){
  const data=posts[props.post];
  return(
    <ScrollAnimation animateOnce="true" className="blogPost" animateOut="fadeOut" animateIn="fadeIn">
    <div >
      <b>{data.title}</b>
      <Link onClick={props.rem} to={`post/${props.post}`}><img src={data.cover}/></Link>
      <p>{data.caption}</p>
    </div>
    </ScrollAnimation>
  );
}

function BlogPost(props){
  const data=posts[props.match.params.id];
  console.log("Sfgfsg");
  
  return(
    <div className="blogPage">
      <div className="postTopSection">
        <button className="backButton mob" onClick={props.history.goBack}>Go Back</button>
        <b className="titleText mob">{data.title}</b>
        <div className="postImages">
          <img src={data.cover}/>
          {data.images.map(item=><img src={item}/>)}
        </div>
      </div>
      <section className="textContent">
        <button className="backButton nonmob" onClick={props.history.goBack}>Go Back</button>
        <b className="titleText nonmob">{data.title}</b>
        <p className="caption">{data.caption}</p>
  {data.text.map(item=><p>{item.map(e=><div>{e}</div>)}</p>)}
      </section>
    </div>
  );
}
export default App;
